/**
 * 
 * @module $ui/components/my-editor
 */

import Editor from '../common/Editor'
import Base from '../Base'

export default {
  name: 'MyEditor',
  mixins: [Base],
  components: {
    Editor
  },
  /**
   * 
   * @member props
   * @property {string} [value] 
   * @property {string|array} toolbar  'simple', 'classic', 'all'
   * @property {object} [config] keditor5 
   * @property {number} [height] 
   * @property {boolean} [readonly] 
   * @property {boolean} [disabled] 
   */
  props: {
    value: String,
    height: Number,
    toolbar: [String, Array],
    // 
    stopEnterEvent: {
      type: Boolean,
      default: true
    }
  },
  render() {
    const vnode = (
      <Editor ref="comp"
              {...{
                props: {...this.innerProps, height: this.height, toolbar: this.toolbar},
                on: {
                  ...this.$listeners
                }
              }}
              vModel={this.fieldValue}>
      </Editor>
    )
    
    return this.renderComponent(vnode)
  },
  mounted() {
    this.extendMethods(this.$refs.comp, ['setData', 'getData'])
  }
}
